<script>
import axios from 'axios'

export default {
  name: 'singleGet',
  methods: {
    async getSingle() {
      const net = '' 
      const net_files = `${net}/files`
      const jsonFile = `${net_files}/infos/infos.json`

      const net_assets_ill = `${net}/assets/illLow`;

      try {
        const response = await axios.get(jsonFile)
        const jsonData = response.data
        const chapterGroup = {};
        
        const OTHER_CHAPTER_KEY = "Other 其它曲目";
        
        for (const [levelId, levelInfo] of Object.entries(jsonData)) {
          if (!levelId || !levelInfo.name) continue;
          
          let chapterKey;
          if (levelInfo.display) {
            chapterKey = levelInfo.chapter;
            if (!chapterKey) {
              chapterKey = OTHER_CHAPTER_KEY;
            }
          } else {
            chapterKey = OTHER_CHAPTER_KEY;
          }
          
          if (!chapterGroup[chapterKey]) {
            chapterGroup[chapterKey] = [];
          }
          
          chapterGroup[chapterKey].push([
            levelId,                  
            chapterKey,
            `${net_assets_ill}/${levelId}.png`  
          ]);
        }

        return chapterGroup;
      } catch (error) {
        console.error('获取数据失败：', error);
        console.error('实际请求的JSON文件路径：', jsonFile);
        return {};
      }
    }
  }
}
</script>