<template>
  <div class="music-detail-card">
    <div class="top-info">
      <p class="music-chapter">{{ dict.chapter || '-' }}</p>
      <h2 class="music-name">{{ dict.name || '-' }}</h2>
      <div class="composer-chapter-wrap">
        <p class="music-composer">{{ dict.composer || '-' }}</p>
      </div>
    </div>

    <div class="image-container">
      <img
        :src="imagePath"
        :alt="dict.name || '曲绘'"
        class="illustration-image"
        @error="isImageError = true"
      />
      <div v-if="isImageError" class="image-placeholder">
        暂无图片
      </div>
    </div>

    <div class="bottom-info">
      <p class="music-illustrator">{{ dict.illustrator || '-' }}</p>
    </div>

    <div class="other-info">
      <div class="info-grid">
        <div v-for="(field, index) in otherFields" :key="index" class="info-item">
          <span class="info-label">{{ field.label }}：</span>
          <span class="info-value">{{ dict[field.key] || '-' }}</span>
        </div>
      </div>
    </div>

    <div class="difficulty-section">
      <div class="table-wrapper">
        <table class="difficulty-table">
          <thead>
            <tr>
              <th>难度</th>
              <th>定数</th>
              <th>物量</th>
              <th>谱师名义</th>
              <th>谱师</th>
              <th>Tap</th>
              <th>Drag</th>
              <th>Hold</th>
              <th>Flick</th>
              <th>Line</th>
            </tr>
          </thead>
          <tbody>
            <tr 
              v-for="(item, index) in validDifficultyData" 
              :key="index"
            >
              <td class="diff-name" :style="{ backgroundColor: difColor_map[item.diffName] }">{{ item.diffName }}</td>
              <td>{{ item.data.lvl || '-' }}</td>
              <td>{{ item.data.notecount || '-' }}</td>
              <td>{{ item.data.charter || '-' }}</td>
              <td>{{ item.data.tcharter || '-' }}</td>
              <td>{{ item.data.tap ?? '-' }}</td>
              <td>{{ item.data.drag ?? '-' }}</td>
              <td>{{ item.data.hold ?? '-' }}</td>
              <td>{{ item.data.flick ?? '-' }}</td>
              <td>{{ item.data.line ?? '-' }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, ref, computed, onMounted } from 'vue'

const otherFields = [
  { label: '更新', key: 'date' },
  { label: '版本', key: 'version' },
  { label: 'BPM', key: 'BPM' },
  { label: '时长', key: 'duration' },
  { label: '关联', key: 'relation' },
  { label: '赛事', key: 'competition'}
];

const difficultyKeys = ['EZ', 'HD', 'IN', 'AT', 'LY', 'SP'];

const difColor_map = {
  'EZ': '#0D9A28',
  'HD': '#0175B8',
  'IN': '#CF1313',
  'AT': '#383838',
  'SP': '#383838',
  'LY': '#383838'
}

const props = defineProps({
  dict: {
    type: Object,
    required: true,
    default: () => ({})
  }
})

const imagePath = ref('')
const isImageError = ref(false)

const getIllAssetsPath = computed(() => {
  if (props.dict?.illAssets && typeof props.dict.illAssets === 'string') {
    return props.dict.illAssets
  }
  return ''
})

const difficultyTypes = computed(() => {
  const result = {};
  difficultyKeys.forEach(key => {
    result[key] = props.dict[key] || {};
  });
  return result;
});

const validDifficultyData = computed(() => {
  const validData = [];
  for (const diffName of difficultyKeys) {
    const diffData = difficultyTypes.value[diffName] || {};
    if (!isDiffEmpty(diffData)) {
      validData.push({
        diffName,
        data: diffData
      });
    }
  }
  return validData;
});

const isDiffEmpty = (diffData) => {
  return !diffData.tcharter && !diffData.dif && !diffData.lvl && !diffData.notecount;
}

const initImage = () => {
  imagePath.value = getIllAssetsPath.value
  isImageError.value = false
}

onMounted(() => {
  initImage()
})
</script>

<style scoped>
.music-detail-card {
  width: 75%;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  background-color: var(--color-bg-sub);
  gap: 12px;
}

.top-info {
  text-align: center;
}

.music-name {
  font-size: 32px;
  font-weight: bold;
  color: var(--color-text);
}

.composer-chapter-wrap {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.music-composer {
  font-size: 18px;
  margin: 0;
  color: var(--color-text);
}

.music-illustrator {
  font-size: 16px;
  margin: 0;
  color: var(--color-text);
}

.music-chapter {
  font-size: 20px;
  margin: 0;
  color: var(--color-text);
}

.image-container {
  width: 100%;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.illustration-image {
  width: 100%;
  height: auto;
  display: block;
}

.image-placeholder {
  padding: 60px 20px;
  background-color: var(--color-bg-sub);
  text-align: center;
  font-size: 14px;
  border: 1px dashed var(--color-text-sub);
}

.illustration-image[src=""] {
  display: none;
}

.bottom-info {
  text-align: center;
}

.other-info {
  margin-top: 0px;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px 16px;
}

.info-item {
  display: flex;
  align-items: center;
  padding: 6px;
  border: 1px solid var(--color-text-sub);
}

.info-label {
  font-weight: 600;
  color: var(--color-text);
  min-width: 60px;
}

.info-value {
  color: var(--color-text);
  flex: 1;
}

.difficulty-section {
  margin-top: 20px;
  width: 100%;
}

.table-wrapper {
  width: 100%;
  overflow-x: auto;
}

.difficulty-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 800px;
}

.difficulty-table th,
.difficulty-table td {
  border: 1px solid var(--color-text-sub);
  padding: 8px 12px;
  text-align: center;
}

.difficulty-table th {
  background-color: #1f1f1f;
  font-weight: 600;
  color: var(--color-text);
}

.difficulty-table .diff-name {
  font-weight: bold;
  color: white;
}
</style>