<script>
import axios from 'axios'

export default {
  name: 'singleInfoGet',
  methods: {
    async getSingleInfo(firstLevelName) {
      const net = '' 
      const net_files = `${net}/files`
      const jsonFile = `${net_files}/infos/infos.json`
      const net_assets_ill = `${net}/assets/ill`;

      try {
        const response = await axios.get(jsonFile)
        const jsonData = response.data;
        
        if (Object.prototype.hasOwnProperty.call(jsonData, firstLevelName)) {
          const originalData = jsonData[firstLevelName];
          const result = typeof originalData === 'object' && originalData !== null 
            ? {
                ...originalData,
                illAssets: `${net_assets_ill}/${firstLevelName}.png`
              }
            : {
                value: originalData,
                illAssets: `${net_assets_ill}/${firstLevelName}.png`
              };
              
          return result;
        } else {
          console.warn(`未找到键名为 "${firstLevelName}" 的数据`);
          return null;
        }
      } catch (error) {
        console.error('获取数据失败：', error);
        console.error('实际请求的JSON文件路径：', jsonFile);
        return null;
      }
    }
  }
}
</script>